import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Col } from 'antd';
import { ColEditDelete, TableData } from 'components/common/index';
import { getPrizeNotes } from 'services/addPrizeNote';
import { getQueryParams } from 'utils/urlUtil';
import { dateToString } from 'utils/formatUtil';
import FormFilterScholarship from './FormFilterScholarship';

class AddScholarshipPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  render() {
    const columns = [
      {
        title: 'Mã SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        render: (text, record) => dateToString(record.dob, 'DD/MM/YYYY'),
      },
      {
        title: 'Nơi sinh',
        dataIndex: 'pob',
        key: 'pob',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Số QĐ',
        dataIndex: 'approvalNumber',
        key: 'approvalNumber',
      },
      {
        title: 'Ngày QĐ',
        dataIndex: 'approvalDate',
        key: 'approvalDate',
        render: (text, record) => dateToString(record.approvalDate, 'DD/MM/YYYY'),
      },
      {
        title: 'Nội dung',
        dataIndex: 'description',
        key: 'description',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#/scholarship/updateForm//${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    const listParamSearch = this.state.paramSearch;
    return (
      <Card title="Nhập khen thưởng">
        <Row>
          <Col span={24}>
            <FormFilterScholarship />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/scholarship/addForm" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getPrizeNotes}
              allowGetData={
                !!listParamSearch.trainTypeId &&
                !!listParamSearch.facultyId &&
                !!listParamSearch.courseId &&
                !!listParamSearch.studyFieldId &&
                !!listParamSearch.yearEduId &&
                !!listParamSearch.semesterId
              }
              paramSearch={this.state.paramSearch}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AddScholarshipPage />
  </Page>
);



