import React, { Component } from 'react';
import { Col, Form, Input } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { getQueryParams, udpateQueryParams } from 'utils/urlUtil';
import './style.scss';

@Form.create({ name: 'formTeacher' })
class FormFilterScholarship extends Component {
  state = {};
  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <FilterPathEducate form={this.props.form} />
        <YearSemesterInput form={this.props.form} />
        <Col span={6}>
          <Form.Item label="Mã SV">
            {getFieldDecorator('email', { initialValue: '' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item label="Họ tên">
            {getFieldDecorator('email', { initialValue: '' })(<Input />)}
          </Form.Item>
        </Col>
        <Col span={6}>
          <Form.Item label="">
            <BtnSearch htmlType="submit" />
            <BtnClear onClick={this.handleReset} />
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormFilterScholarship;



